import { Inline, InlineProperties, Data } from '@ali/4ever-cangjie';
import type { MoInterfaces } from '@ali/4ever-cangjie';
import { ELEMENT_TYPE } from '../../utils/meta';

export interface ImageData extends Data {
  readonly src?: string;
  readonly width?: number;
  readonly height?: number;
  readonly outline?: {
    readonly width?: number;
    readonly type?: string;
    readonly color?: string;
  };
  readonly rectClip?: {
    readonly right?: number;
    readonly left?: number;
    readonly top?: number;
    readonly bottom?: number;
  };
  readonly rotation?: number;
  readonly radius?: number;
  readonly shadow?: string;
  readonly extraData?: any;

  // NOTE: 目前无消费 name & opacity & lockRatio
  readonly name?: string;
  readonly size?: number;
  readonly opacity?: number;
  readonly lockRatio?: number;
  readonly wrap?: MoInterfaces.WrapPr.WrapPr;
}

export interface ImageProperties {
  data?: ImageData;
  nodes?: InlineProperties['nodes'];
}

export default class Image extends Inline {
  static TYPE = ELEMENT_TYPE;

  static create(properties: Image | ImageProperties | string) {
    if (typeof properties === 'string') {
      throw new Error('`Image.create` does not accept string');
    }
    if (Image.isImage(properties)) {
      return properties;
    }
    return super.create({
      ...properties,
      type: Image.TYPE,
    }) as Image;
  }

  static isImage(maybeImage: any): maybeImage is Image {
    return Inline.isInline(maybeImage) && maybeImage.type === Image.TYPE;
  }
}
